<template>
    <nacl-page-container no-header>
        <!-- <nacl-header transparent /> -->
        <nacl-page-content>
            <div class="content">
                <!-- <h1 class="title">确认订单</h1> -->
                <div class="address-wrap">
                    <nacl-flexbox
                        align="center"
                        @click.native="addressClick"
                    >
                        <img
                            src="../assets/address.png"
                            class="location-img"
                        >
                        <div
                            v-if="addressInfo"
                            class="information"
                        >
                            <div>
                                <span class="name">{{ addressInfo.name }}</span>
                                <span class="phone">{{ addressInfo.tel }}</span>
                            </div>
                            <div class="address">{{ addressInfo.addressDetail }}</div>
                        </div>
                        <span v-else>点击填写收货地址</span>
                    </nacl-flexbox>
                </div>
                <div class="address-wrap">
                    <div class="information coupon" @click="isShowCouponList = true">
                        <div v-if="!activeCoupon">使用优惠卷</div>
                        <div v-else class="red">
                            <span v-if="activeCoupon.min === '0.00'">{{ activeCoupon.discount }}元无门槛优惠卷</span>
                            <span v-else>满{{ activeCoupon.min }}减{{ activeCoupon.discount }}元优惠卷</span>
                        </div>
                    </div>
                </div>
                <ul class="book-list">
                    <li
                        v-for="(item, index) in data"
                        :key="index"
                    >
                        <nacl-flexbox class="book-detail">
                            <img :src="item.thumbnail">
                            <nacl-flexbox-item>
                                <h5>{{ item.title }}</h5>
                                <!-- <p
                                    class="desc"
                                    v-html="item.mediaSale.mediaList[0].descs"
                                /> -->
                                <nacl-flexbox
                                    align="center"
                                    justify="between"
                                >
                                    <p class="amount">¥{{ item.price }}</p>
                                    <span v-if="!$parent.canHandle">x{{ item.buyCount }}</span>
                                </nacl-flexbox>
                            </nacl-flexbox-item>
                        </nacl-flexbox>
                        <nacl-flexbox
                            class="delivery"
                            justify="between"
                        >
                            <span>配送方式:</span><span>包邮</span>
                        </nacl-flexbox>
                    </li>
                </ul>
            </div>
        </nacl-page-content>
        <footer>
            <nacl-flexbox
                class="footer-wrap"
                align="center"
                justify="between"
                id="ceshiaaa"
            >
                <span class="totals">¥{{ total }}</span>
                <a
                    class="buy-btn"
                    @click="isShowPayMethod = true"
                >立即购买</a>
            </nacl-flexbox>
        </footer>
        <div
            v-if="isModal"
            class="modal"
        >
            <div class="modal-content">
                <img src="../assets/order-success.png">
                <h1>下单成功</h1>
                <!-- <p>
                    订单账号: {{ $route.params.id }}
                </p> -->
            </div>
        </div>
        <alter
            title="订单确认"
            text="请确认下单信息无误"
            :is-show="isShowAlter"
            @clickButton="orderConfirmation"
        />
        <div v-html="htmlContent" />

        <div v-show="isShowCouponList" class="coupon-list">
            <div class="box">
                <div>
                    <p class="close" @click="isShowCouponList = false">关闭</p>
                    <ul v-if="couponList.length > 0">
                        <li v-for="(item, index) in couponList" :key="item.id + index" @click="couponSelect(item)">
                            <img src="../assets/coupon-1.png" alt="">
                            <span v-if="item.min === '0.00'">{{ item.discount }}元无门槛优惠卷</span>
                            <span v-else>满{{ item.min }}减{{ item.discount }}元优惠卷</span>
                        </li>
                    </ul>
                    <p v-else class="prompt">暂无符合使用条件优惠卷</p>
                </div>
            </div>
        </div>
        <div v-show="isShowPayMethod" class="payMethod" @click="isShowPayMethod = false">
            <ul class="payList">
                <li @click="handleBuy"><img src="../assets/zhifubao.png">支付宝</li>
                <li><div @click="goCreditCard()"><img src="../assets/xinyongka.png">信用卡</div></li>
            </ul>
        </div>
    </nacl-page-container>
</template>

<script>
import { ordertotalAddOrder, getAddressDefault, getCart, orderSubmit, orderAlipay, getUserData, orderPay201, couponSelectlist } from '../services'
import alter from '../components/alter'

export default {
    name: 'index',
    components: { alter },
    data () {
        return {
            addressInfo: undefined,
            isModal: false,
            isShowAlter: false,
            data: [],
            cartId: this.$route.query.cartId || null,
            htmlContent: '',
            couponList: [],
            activeCoupon: null,
            isShowCouponList: false,
            isShowPayMethod: false
        }
    },
    computed: {
        total () {
            let total = 0
            this.data.forEach((item) => {
                total += item.price * item.buyCount
                // console.log(item)
            })
            if (this.activeCoupon) {
                total -= this.activeCoupon.discount
            }
            // console.log(total, this.activeCoupon)
            total = total < 0 ? 0 : total
            return total.toFixed(2)
        }
    },
    async created () {
        // window.webkit.messageHandlers.showHead.postMessage('');
        // this.data = await this.getData()
        // this.couponList = await couponSelectlist(this.cartId || 0)
        // this.activeCoupon = this.couponList[0]
        // console.log(this.couponList)
        // console.log(this.data)
    },
    async mounted () {
        window.webkit.messageHandlers.showHead.postMessage('');
        this.data = await this.getData()
        this.couponList = await couponSelectlist(this.cartId || 0)
        this.activeCoupon = this.couponList[0]
        this.addressInfo = await getAddressDefault()
        window.goRouting = this.goRouting;
        // console.log("bottom="+document.getElementById("ceshiaaa").getBoundingClientRect().bottom)
    },
    methods: {
        goRouting(param){
            this.toGetUser();
            window.webkit.messageHandlers.goRouting.postMessage(param);
        },
        goCreditCard(){
            this.toGetUser();
            window.webkit.messageHandlers.creditCard.postMessage('');
        },
        toGetUser(){
            const cutCount = Math.ceil(JSON.stringify(localStorage).length / 1000);
            for(let i = 0 ; i<cutCount;i++){
                if(i!=cutCount-1){
                    if(i==0){
                        window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,1000*(i+1))));
                    }else{
                        window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,1000*(i+1))));
                    }
                    
                }else{
                    window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,JSON.stringify(localStorage).length)));
                }
            }
        },
        couponSelect (data) {
            this.activeCoupon = data
            // console.log(data)
            this.isShowCouponList = false
        },
        async handleBuy () {
            if (this.addressInfo) {
                const couponId = this.activeCoupon ? this.activeCoupon.id : 0
                const orderId = await orderSubmit({
                    cartId: this.cartId || 0,
                    addressId: this.addressInfo.id,
                    couponId: couponId
                })
                this.htmlContent = await orderAlipay(orderId)
                await orderPay201(orderId)
                setTimeout(() => {
                    window.webkit.messageHandlers.hideHead.postMessage('');
                    const form = document.getElementsByTagName('form')[0]
                    form.submit()
                }, 100)
                if (getUserData().nickName === '18999999999') {
                    await ordertotalAddOrder()
                    this.isModal = true
                    setTimeout(() => {
                        this.toGetUser();
                        window.webkit.messageHandlers.order.postMessage('');
                        // this.$router.replace('/order')
                    }, 2000)
                }
            } else {
                this.$toast('请填写收货地址')
            }
        },
        addressClick () {
            this.toGetUser();
            window.webkit.messageHandlers.addressList.postMessage('');
            // this.$router.push('/addressList')
        },

        orderConfirmation (nub) {
            this.isShowAlter = false
            if (nub === 0) {
                setTimeout(() => {
                    this.handleBuy()
                }, 500)
            }
        },
        async getData () {
            let cartId = this.cartId
            let data = await getCart(cartId)
            if(data.length==0){
                this.toGetUser();
                window.webkit.messageHandlers.order.postMessage('?showType=1');
            }
            return data.filter((item) => item.checked)
        }
    }
}
</script>

<style scoped lang="scss">
.coupon-list {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 1000;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    .box {
        margin: 50px;
        background: #fff;
        min-height: 800px;
        border-radius: 10px;
        .close {
            padding: 10px 20px;
            text-align: right;
            color: #6e6e6e;
            border-bottom: 1px solid #eee;
        }
        .prompt {
            margin-top: 300px;
            text-align: center;
        }
        img {
            width: 60px;
            margin-right: 20px;
            vertical-align: middle;
        }
        li {
            margin: 20px;
            // border: 1px solid #512ffd;
            padding: 30px 20px;
            box-shadow: 0 0 5px 1px rgba(0,0,0, 0.5);
            border-radius: 10px;
            color: #fff;
            background: linear-gradient(75deg,#8628fa,#512ffd);
        }
    }
}
$footer-height: 58px * 2;
.content {
  padding: 0 34px;
}
.title {
  font-size: 18px * 2;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
}
.address-wrap {
  background: #fff;
  position: relative;
  padding: 36px 40px 36px 40px;
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.08);
  border-radius: 20px;
  font-size: 14px * 2;
  margin-top: 34px;
  .edit-btn {
    display: inline-block;
    position: absolute;
    right: 22px;
    top: 18px;
    width: 16px * 2;
    height: 16px * 2;
    background-image: url("../assets/editor.png");
    background-size: 100%;
  }
  .location-img {
    width: 60px;
    margin-right: 34px;
  }
  .information {
    line-height: 1.5;
    .name {
      margin-right: 60px;
    }
  }
}
.book-list {
  margin-top: 24px;
  li {
    padding: 30px 30px;
    background: #fff;
    border-radius: 20px;
    margin-bottom: 30px;
    box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.08);
  }
  .book-detail {
    img {
      width: 110px * 2;
      height: 110px * 2;
      margin-right: 32px;
      border-radius: 22px;
    }
    h5 {
      font-size: 24px;
      line-height: 1.33;
      color: #2A2A2A;
      overflow: hidden;
      height: 90px;
      margin-bottom: 25px;
      font-weight: 400;
    }
    .author {
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
    .desc {
      word-break: break-all;
      height: 84px * 2;
      line-height: 1.86;
      overflow: hidden;
      margin-top: 24px;
      color: #666666;
    }
    .amount {
      font-size: 28px;
      font-weight: 500;
      color: #FF4600;
    }
  }
  .delivery {
    margin-top: 30px;
    color: #666666;
  }
}
.footer-wrap {
  z-index: 99;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background: #fff;
  height: $footer-height;
  padding: 0 40px;
  box-sizing: border-box;
  .totals {
    font-size: 24px * 2;
    font-weight: 500;
    color: #d0021b;
  }
  .buy-btn {
    width: 124px * 2;
    height: 41px * 2;
    line-height: 41px * 2;
    background: linear-gradient(75deg,#8628fa,#512ffd);;
    color: #fff;
    font-size: 18px * 2;
    text-align: center;
    border-radius: 41px;
  }
}
.modal {
  position: fixed;
  z-index: 999;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.4);
  .modal-content {
    position: relative;
    width: 268px * 2;
    height: 212px * 2;
    background-color: #ffffff;
    margin: 400px auto 0 auto;
    border-radius: 10px;
    text-align: center;
    img {
      width: 71.9px * 2;
      height: 71.9px * 2;
      margin-top: 64px;
    }
    h1 {
      font-size: 18px * 2;
      margin-top: 28px;
    }
    p {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      line-height: 90px;
      border-top: 1px solid #efeded;
      color: #666;
      font-size: 28px;
    }
  }
}
 .payMethod {
        position: fixed;
        z-index: 99999;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.7);
        .payList {
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            background: #fff;
            font-size: 34px;
            text-align: center;
            li {
                height: 150px;
                border-bottom:  1px solid #eee;
                line-height: 150px;
            }
            img {
                max-width: 80px;
                max-height: 80px;
                vertical-align: middle;
                margin-right: 20px;
                margin-top: -10px;
            }
        }
    }
</style>
